import { environment } from "../../environments/environment.prod";
import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Observable } from "rxjs";
import { User } from '../models/user.model';
import { map } from 'rxjs/operators';
import { Post } from "../models/post.model";
// import { Post } from '../models/post.model';

@Injectable({
  providedIn: "root"
})
export class SearchService {
  constructor(private http: HttpClient) {}

  getAllUsers() {
    return this.http.get<User[]>(`${environment.apiUrl}/users`);
  }

  getAllPosts() {
    return this.http.get<Post[]>(`${environment.apiUrl}/posts`);
  }

  searchByPost(searchQuery: string): Observable<Post[]> {
    return this.http.get<Post[]>(
      `${environment.apiUrl}/posts?q=${searchQuery}`
    );
  }

  getUserByEmail(email:string): Observable<User>{
    return this.http.get(`${environment.apiUrl}/users?email=${email}`).pipe(map((user: User[]) => user[0] ? user[0] : undefined));
  }

  getUserById(id:number): Observable<User>{
    return this.http.get(`${environment.apiUrl}/users?id=${id}`).pipe(map((user: User[]) => user[0] ? user[0] : undefined));
  }

  getPostById(id: number): Observable<Post>{
    return this.http.get<Post>(`${environment.apiUrl}/posts/${id}`)
    .pipe(map((post: Post) => post ? post : undefined));
  }
}
