import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { environment } from 'src/environments/environment.prod';
import { Post } from '../models/post.model';
import { Request } from '../models/request.model';

@Injectable({
  providedIn: 'root'
})
export class ManagerService {

  constructor(private http: HttpClient) {}

  getAllRequests(){
    return this.http.get(`${environment.apiUrl}/requests`);
  }

  getAllPosts() {
    return this.http.get<Post[]>(`${environment.apiUrl}/posts`);
  }

  addPost(post: Post): Observable<Post>{
    return this.http.post<Post>(`${environment.apiUrl}/posts`, post);
  }

  removePost(post: Post): Observable<Post>{
    return this.http.delete<Post>(`${environment.apiUrl}/posts/${post.id}`);
  }

  editPost(post: Post): Observable<Post>{
    return this.http.put<Post>(`${environment.apiUrl}/posts/${post.id}`, post);
  }

  editRequest(reqId, postId){
    return this.http.patch(`${environment.apiUrl}/requests/${reqId}`, {postId: postId});
  }

  removeRequest(reqId){
    return this.http.delete(`${environment.apiUrl}/requests/${reqId}`)
  }
  sendNotification(userId, notes){
    return this.http.patch(`${environment.apiUrl}/users/${userId}`, {notifications: notes})
  }
}
