import { DatePipe } from '@angular/common';
import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Request } from 'src/app/models/request.model';
import { RequestService } from 'src/app/services/request.service';
import { UserStateService } from 'src/app/services/user-state.service';

@Component({
  selector: 'app-post',
  templateUrl: './post.component.html',
  styleUrls: ['./post.component.css'],
  providers: [DatePipe]
})
export class PostComponent implements OnInit {

  @Input() post;

  constructor(
    private router: Router, 
    private requestService: RequestService,
    private userState: UserStateService,
    private datePipe: DatePipe){}

  ngOnInit () {
   
  }

  toPage() {
   this.router.navigate([`/post`, this.post.id]);
  }

  makeRequest(postId: number){
    let date:string = this.datePipe.transform(Date.now(), 'dd-MM-yyyy');
    let authorId:number = this.userState.getUser().id;
    // console.log(date, userId);
    const request = new Request().buildPost({authorId, postId, date});
    console.log(request);
    this.requestService.addRequest(request).subscribe();
  }
}
