import { HttpErrorResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Post } from 'src/app/models/post.model';
import { ManagerService } from 'src/app/services/manager.service';
import { SearchService } from 'src/app/services/search.service';
import { UserStateService } from 'src/app/services/user-state.service';

@Component({
  selector: 'app-post-manager',
  templateUrl: './post-manager.component.html',
  styleUrls: ['./post-manager.component.css']
})
export class PostManagerComponent implements OnInit {

  form: FormGroup;
  formEdit: FormGroup;
  post: Post;
  isExist: boolean = true;

  constructor(
    private formBuilder: FormBuilder, 
    private searchService: SearchService, 
    private managerService: ManagerService,
    private router: Router,
    private userState: UserStateService
  ) {}

  ngOnInit() {
    if(!this.userState.isAdmin()){
      this.router.navigate(["/"])
    }
    this.form = this.formBuilder.group({
      title: [""],
      src: ["", [Validators.required]],
      desc: [""]
    });
    this.formEdit = this.formBuilder.group({
      idEdit: ["", [Validators.required]],
      titleEdit: [""],
      srcEdit: [""],
      descEdit: [""]
    });
  }

  onSubmit() {
    const post = new Post().buildPost(this.form.value);
    this.managerService.addPost(post)
    .subscribe(data => { this.form.reset() })
  }

  edit(){
    const post = new Post().buildPut(this.formEdit.value);
    this.managerService.editPost(post)
    .subscribe(data => {this.formEdit.reset()})
  }

  deletePost(){
    const post = new Post().buildPut(this.formEdit.value);
    this.managerService.removePost(post)
    .subscribe(data => {this.formEdit.reset()})
  }

  onChangeId(){
    this.searchService.getPostById(this.formEdit.value.idEdit)
    .subscribe( (data) => {
        this.post = data;
        this.isExist = true;
        // console.log(myForm);
        this.formEdit.patchValue({
          titleEdit: this.post.title,
          srcEdit: this.post.src,
          descEdit: this.post.desc
        });
    }, 
    (err: HttpErrorResponse) => {
      this.isExist = false;
      //console.log(this.isExist);
    }
    );
  }

  get src() {
    return this.form.get('src');
  }

  get idEdit() {
    return this.formEdit.get('idEdit');
  }

  get titleEdit() {
    return this.formEdit.get('titleEdit');
  }

  get srcEdit() {
    return this.formEdit.get('srcEdit');
  }

  get descEdit() {
    return this.formEdit.get('descEdit');
  }
}
