import { Injectable } from "@angular/core";
import { User } from "../models/user.model";
import { Router } from '@angular/router';
import { SearchService } from "./search.service";

const LSUserKey = "user";

@Injectable({
  providedIn: "root"
})
export class UserStateService {
  private user: User;
  countOfNotes: number;
  constructor(private router: Router) {}

  setUser(user: User) {
    this.user = user;
    localStorage.setItem(LSUserKey, JSON.stringify(user));
    this.countOfNotes = this.user.notifications.length;
  }

  getUser(){
    return this.user;
  }

  deleteUser(){
    this.user = undefined;
    localStorage.removeItem(LSUserKey);
  }

  logOut() {
    this.user = undefined;
    localStorage.removeItem(LSUserKey);
    this.router.navigate(["/login"]);
  }

  fetch() {
    let user = localStorage.getItem(LSUserKey);
    let newUser = user !== null && user !== undefined ? JSON.parse(user) : undefined;
    if (newUser !== undefined) {
      this.countOfNotes = newUser.notifications.length;
      this.user = new User(newUser.userName, newUser.email, newUser.password, newUser.messages, newUser.notifications, newUser.id);
    }
  }

  isAuth(): boolean {
    return this.user != undefined;
  }

  isAdmin(): boolean{
    if(this.user == undefined){return false} else
    return this.user.id == 1;
  }
}
