import { HomeComponent } from "./home/home.component";
import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { PostManagerComponent } from "./post-manager/post-manager.component";
import { RequestManagerComponent } from "./request-manager/request-manager.component";
import { ChatComponent } from "./chat/chat.component";
import { NotificationsComponent } from "./notifications/notifications.component";
import { ManageChooseComponent } from "./manage-choose/manage-choose.component";

const routes: Routes = [
  {
    path: "",
    component: HomeComponent
  },
  {
    path: "manageChoose",
    component: ManageChooseComponent
  },
  {
    path: "post-manager",
    component: PostManagerComponent
  },
  {
    path: "chat",
    component: ChatComponent
  },
  {
    path: "request-manager",
    component: RequestManagerComponent
  },
  {
    path: "notifications",
    component: NotificationsComponent
  },
  {
    path: "chat/:id",
    component: ChatComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SystemRoutingModule {}
